	module.exports = {"data":{"site":{"siteMetadata":{"title":"Edmundo Santos"}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/blog/the-scariest-moment/"},"html":"<p>Happy days: today I‚Äôm releasing a new version of my website! üéâ</p>\n<p>I decided to throw away the <a href=\"https://github.com/edmundojr/edmundojr-2014\">previous one (‚ú¶ 2014 - ‚ô± 2018)</a> and build this one from scratch using <a href=\"https://www.gatsbyjs.org/\">Gatsby.js</a>, a static site generator for React.js.</p>\n<p>Because I was already learning about the new CSS Grid system, I decided to use it for designing this baby. I started with some rough sketches on Sketch (ah ha!) and then moved on to design in the browser. FirefoxDeveloperEdition has some <a href=\"https://mozilladevelopers.github.io/playground/css-grid\">excellent tools</a> for working with CSS Grid layout, I highly recommend it.</p>\n<p>Here‚Äôs a list of things I plan to do next with this website, in no particular order:</p>\n<ul>\n<li>Build a <strong>library</strong> page with books I‚Äôm reading, books I‚Äôve read, and books I‚Äôm gonna read</li>\n<li>Write <strong>articles</strong> about stuff I‚Äôm working on, what I‚Äôm learning, or whatever I feel like writing really</li>\n<li>It would be cool to have a <strong>photography</strong> page where I can share some of my pics too, definitely going to copy some ideas from <a href=\"https://paulstamatiou.com/photos/\">@stammy‚Äôs website</a></li>\n<li>Build an <strong>about</strong> page (meh)</li>\n</ul>\n<p>The website is not fully responsive yet and there are probably lots of bugs, but oh well. Please <a href=\"https://github.com/edmundojr/edmundojr.github.io/issues/new\">report</a> if you find any bugs. üêõ</p>\n<p>On each and every one of the blogs I‚Äôve started in the past I posted this quote, so here it is again:</p>\n<blockquote>\n<p>‚ÄúThe scariest moment is always just before you start. After that, things can only get better.‚Äù</p>\n<footer>‚Äî Stephen King</footer>\n</blockquote>\n<p>That‚Äôs it for today, <a href=\"https://twitter.com/edmundojr_\">let me know</a> what you think.</p>","frontmatter":{"date":"18 Feb 2018","title":"The Scariest Moment","tags":["Personal"]}}}]}},"pathContext":{}}

